@import './theme/material/_typo.css';
.debugBaseLine {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
  height: 100%;
  background: linear-gradient(var(--color-baseline), var(--color-baseline) 1px, transparent 1px);
  background-size: 1px calc(1rem * var(--base-line));
  background-position: 0px 0.5px;
	z-index: 9999;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	pointer-events: none;
	opacity: 0;
}
#app > input {
	position: fixed;
	top: 0;
	right: 0;
	z-index: 9000;
}
input:checked + .debugBaseLine {
	opacity: 1;
}
.flexRow {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: start;
	    -ms-flex-align: start;
	        align-items: flex-start;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}
